<template>

  <div>
    <el-row>
      <el-col :span="3">
        <!-- c1.jpg 2.jpg 3.jpg -->
        <!-- <el-button type="primary" @click="addAll"></el-button> -->
        <!-- <el-upload class='upload-demo' multiple accept='.jpg' ref='upload' :action='updateurl' :on-success='handleSuccess' :auto-upload='true' :show-file-list='false'>
          <el-button type='primary' size="small"></el-button>
        </el-upload> -->

                <el-upload class='upload-demo' multiple  ref='upload' :action='updateurl' :on-success='handleSuccess' :auto-upload='true' :show-file-list='false'>
          <el-button type='primary' size="small"></el-button>
        </el-upload>
        <br>
      </el-col>
      <el-col :span="3">
        <!-- D -->
        <el-button type="success" size="small" @click="downloadAll"></el-button>
      </el-col>
      <el-col :span="3">
        <!-- images.zip -->
        <el-button type="warning" size="small" @click="downloadRemote">file.zip</el-button>
      </el-col>
      <el-col :span="3">
        <!--  -->
        <el-button type="danger" size="small" @click="deleteAll"></el-button>
      </el-col>
    </el-row>
    <el-row>
      <el-carousel :interval="4000" type="card" height="400px">
        <el-carousel-item v-for="item in imgs" :key="item">
          <img :src="item">
        </el-carousel-item>
      </el-carousel>
    </el-row>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'HelloWorld',
  mounted: function () {
    // 
    this.getImages()
  },
  data () {
    return {
      updateurl: '/updateurl',
      imgs: []
    }
  },
  methods: {
    handleSuccess: function () {
      this.getImages()
    },
    addAll: function () {
      axios
        .post('/addAll')
        .then(Response => {
          this.$alert(Response.data, '', {
            confirmButtonText: '',
            type: 'success'
          }).then(() => {
            this.getImages()
          })
        })
        .catch(() => {
          this.$alert('', '', {
            confirmButtonText: '',
            type: 'error'
          })
        })
    },
    downloadAll: function () {
      axios
        .get('/downloadAll')
        .then(Response => {
          this.$alert(Response.data, '', {
            confirmButtonText: '',
            type: 'success'
          }).then(() => {
            this.getImages()
          })
        })
        .catch(() => {
          this.$alert('', '', {
            confirmButtonText: '',
            type: 'error'
          })
        })
    },
    downloadRemote: function () {
      window.open('/downloadRemote')
    },
    deleteAll: function () {
      axios
        .delete('/deleteAll')
        .then(Response => {
          this.$alert(Response.data, '', {
            confirmButtonText: '',
            type: 'success'
          }).then(() => {
            this.getImages()
          })
        })
        .catch(() => {
          this.$alert('', '', {
            confirmButtonText: '',
            type: 'error'
          })
        })
    },
    // 
    getImages: function () {
      axios
        .get('/getImage')
        .then(Response => {
          if (
            Response.data !== null &&
            Response.data !== undefined &&
            Response.data !== ''
          ) {
            this.imgs = Response.data
          }
        })
        .catch(() => {
          this.$alert('', '', {
            confirmButtonText: '',
            type: 'error'
          })
        })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.el-carousel__item h3 {
  color: #475669;
  font-size: 14px;
  opacity: 0.75;
  line-height: 200px;
  margin: 0;
}
.el-carousel__item:nth-child(2n) {
  background-color: #99a9bf;
}
.el-carousel__item:nth-child(2n + 1) {
  background-color: #d3dce6;
}
</style>



