        <el-col :offset="1" :span="4">
            <el-card class="box-card" style="height:38px;padding-top:0px" >
            
          </el-card>
        </el-col>
        <el-col :span="3">
           
           <el-button type="info"  @click="openNotice" plain round></el-button>
        </el-col>
        
      <el-col :offset="1" :span="4">
        <el-form-item  prop="id">
        <el-input
          placeholder=""
          v-model.number="ruleForm.id"
          clearable>
        </el-input>
