              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
           </el-form-item>
        </el-col>
        <el-col :span="2">
           <el-form-item  prop="age">
          <el-input
          placeholder=""
          v-model.number="ruleForm.age"
          clearable>
        </el-input>
           </el-form-item>
        </el-col>
        <el-col :span="3">
           <el-button type="primary" plain @click="saveInfo('ruleForm')" :disabled="infoDisabled"></el-button>
        </el-col>
        </el-form>
    </el-row>
      <!--  end -->
      <br><br>
    </el-row>

