<script>
export default {
   mounted(){
    this.over = this.RandomNum()
  },
    data () {
    return {
      ruleForm:{
        id:'',
        gender:'',
        age:''
      },
      name:'',
      times: 0,
      initialScore :0,
      sumScore: 0,
      lastTimeScore:0,
      // 
      clickTimes:0,
      bumNum:0,
       options: [{
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        }
        ],
        value4: '',
        age:'',
        leftTime:30,
        // 
        balloonHeight:40,
        balloonWidth:40,
        magrintop:200,
        infoDone:false,
        over:'',
        rules: {
          id: [
             { required: true, message: ''},
            { type: 'number', message: ''}
          ],
          gender: [
            { required: true, message: '', trigger: 'change' }
          ],
          age: [
            { required: true, message: ''},
            { type: 'number', message: ''}
          ]
    }

    }
  },
    
  computed:{
    ifDisabled(){
      return this.initialScore===0;
    },
    infoDisabled(){
      return this.infoDone
    },
    balloonSize(){
      return{
        'height':`${this.balloonHeight}px`,
        'width' :`${this.balloonWidth}px`
      }

      
    }
  },
 
  methods:{
    openNotice(){
           this.$alert('.......', '', {
          confirmButtonText: '',
          callback: action => {
            this.$message({
              type: 'success',
              message: ''
            });
          }
        });
    },
    saveInfo(formName){
      this.$refs[formName].validate((valid) => {
          if (valid) {
            this.infoDone = true;
            this.$message({
          message: '',
          type: 'success'
        });
          } else {
            console.log('error submit!!');
            return false;
          }
        });
  
    },
    clickBalloon(){
      if(this.infoDone){
         if(this.leftTime>0){
         this.magrintop-=2;
        this.balloonWidth+=2;
        this.balloonHeight+=2;
        this.initialScore+=1;
        this.clickTimes+=1;

            if(this.initialScore<=this.over){
              // 
            }else{
              // 
              //
               this.balloonWidth=40;
               this.balloonHeight=40;
               this.magrintop=200;
               //
               //this.lastTimeScore = this.clickTimes
               // 
              this.initialScore = 0;
              this.hadBoomed();
              this.leftTime-=1;
              this.over = this.RandomNum()

            }
      }

      }
      else{
        this.$message.error('!');
      }
     
    
    },
     // 
    getReward(){
      //0
      if(this.leftTime>0){
          // 
          this.sumScore += this.initialScore;
          // 0
          this.initialScore = 0;
          // 1
          this.leftTime -= 1;
                //
               this.lastTimeScore = this.clickTimes
               this.clickTimes = 0
          // 
          this.balloonWidth=40;
          this.balloonHeight=40;
          this.magrintop=200;

      }
     

    },
    hadBoomed() {
        this.$message({
          showClose: true,
          message: '',
          center: true,
          type: 'warning',
          duration:'2000'
        });
      },
      RandomNum() {
            let Min = 0;
            let Max = 128;
             var Range = Max - Min;
             var Rand = Math.random();
             if(Math.round(Rand * Range)==0){             
                 return Min + 1;
             }
             var num = Min + Math.round(Rand * Range);
             return num;
       }
  }
}
</script>

<style type="text/css">
.imgPosi{
  margin: auto
}
.el-card__body{
  padding:7px !important;
}
.scoreSize{
  height: 50px;
  width: 40px;
}

.leftTime{
  color:rgb(6, 30, 136);
  font-size:110%
}
.singleFont{
  color: rgb(179, 20, 20) !important;
  padding:5px;
  font-size: 120%;
}
.balloonPosition{
  text-align:center
}
/* .ballimg {
  position: absolute;
  left: 30px;
  top: 20px;
  bottom: 0px;
} */
        #div1{
          
            width:100px;
            height:100px;
            border-radius: 50%;
            background-color: skyblue;
           /* background-image: url('qq.jpg');*/
        }
        .proHeight{
          height: 120px;
          width: 70px;
          margin:0px;
        }
        .colHeight{
          height: 100px !important;
          width: 35px !important;
        }
        .box-card1{
          background-color: rgb(141, 3, 3);
          height: 100%;
      
        }
         .box-card2{
          background-color: rgb(92, 21, 139);
          height: 100%;
        }
         .box-card3{
          background-color: rgb(36, 46, 184);
          height: 100%;
        } 
        .box-card4{
          background-color: rgb(18, 105, 145);
          height: 100%;
        }
    </style>




